import React, { Component } from "react";
import "./rechart.scss";
import {
  AreaChart,
  Area,
  XAxis,
  YAxis,
  CartesianGrid,
  ResponsiveContainer,
  Tooltip
} from "recharts";

export default class rechart extends Component {
  constructor(props) {
    super(props);
    this.state = { key: props.name };
  }
  render() {
    const { data, name } = this.props;
    return (
      <AreaChart
        width={171}
        height={20}
        data={data}
        margin={{
          top: 5,
          right: 0,
          left: 0,
          bottom: 5
        }}
      >
        <defs>
          <clipPath id="recharts5-clip">
            <rect x="5" y="5" height="10" width="161"></rect>
          </clipPath>
        </defs>
        <defs>
          <linearGradient id="gradient_area" x1="0" y1="0" x2="0" y2="1">
            <stop offset="25%" stopColor="#7987a1" stopOpacity="0.8"></stop>
            <stop offset="50%" stopColor="#82ca9d" stopOpacity="0"></stop>
          </linearGradient>
        </defs>
        {name ? (
          <Area
            type="linear"
            dataKey={name}
            stroke="#7987a1"
            fillOpacity={0.6}
            fill="url(#gradient_area)"
          />
        ) : (
          <h2>loading!</h2>
        )}
      </AreaChart>
    );
  }
}
