import React, { Component } from "react";
import "./login.scss";
import { Alert } from "reactstrap";
import { Redirect } from "react-router-dom";
// import { connect } from "react-redux";
// import { bindActionCreators } from "redux";
// import * as loginActions from "../../store/login/actions";
export default class Login extends Component {
  constructor(props) {
    super(props);
    this.state = {
      email: "",
      password: "",
      error: false
    };
    this.handleEmail = this.handleEmail.bind(this);
    this.handlePassword = this.handlePassword.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  handleEmail(event) {
    this.setState({ email: event.target.value });
  }
  handlePassword(event) {
    this.setState({ password: event.target.value });
  }
  handleSubmit(event) {
    if (
      this.state.email == "admin@plume.lab" &&
      this.state.password == "@dminPass"
    ) {
      localStorage.setItem("Auth", true);
      this.forceUpdate();
    } else {
      this.setState({ error: true });
    }
  }
  render() {
    let auth = localStorage.getItem("Auth");
    if (auth) return <Redirect to="/data"></Redirect>;
    return (
      <div className="container">
        <div className="columns flex-centered">
          <div className="card">
            <article className="card-body">
              <h4 className="card-title mb-4 mt-1">Sign in</h4>
              <form onSubmit={this.handleSubmit}>
                <div className="form-group">
                  <label>Your email</label>
                  <input
                    name=""
                    className="form-control"
                    placeholder="Email"
                    value={this.state.email}
                    onChange={this.handleEmail}
                    type="email"
                  />
                </div>
                <div className="form-group">
                  <label>Your password</label>
                  <input
                    className="form-control"
                    placeholder="******"
                    type="password"
                    value={this.state.password}
                    onChange={this.handlePassword}
                  />
                </div>
                <div className="form-group">
                  <div className="checkbox">
                    <label>
                      <input type="checkbox" /> Save password
                    </label>
                  </div>
                </div>
                <div className="form-group">
                  <button type="submit" className="btn btn-primary btn-block">
                    login
                  </button>
                </div>
              </form>
              <Alert isOpen={this.state.error} color="danger">
                Wrong Password , Check Again
              </Alert>
            </article>
          </div>
        </div>
      </div>
    );
  }
}
