import React, { Component } from "react";
import "./barChart.scss";
import RandomColor from "randomcolor";
import {
  BarChart,
  Bar,
  Cell,
  XAxis,
  YAxis,
  CartesianGrid,
  ResponsiveContainer,
  Tooltip,
  Legend
} from "recharts";
export default class barChart extends Component {
  state = {
    data: [
      {
        name: "Jedah",
        cases: 15
      },
      {
        name: "Jedah2",
        cases: 20
      },
      {
        name: "Jedah3",
        cases: 15
      },
      {
        name: "Jedah4",
        cases: 37
      },
      {
        name: "Jedah5",
        cases: 12
      },
      {
        name: "Jedah6",
        cases: 50
      },
      {
        name: "Jedah7",
        cases: 35
      },
      {
        name: "Jedah8",
        cases: 10
      },
      {
        name: "Jedah9",
        cases: 70
      }
    ]
  };
  render() {
    const { data } = this.props;
    return (
      <div className="container">
        <div style={{ width: "100%", height: 300 }}>
          <ResponsiveContainer>
            <BarChart data={data} layout="vertical">
              <XAxis type="number" />
              <YAxis type="category" padding={{ left: 20 }} dataKey="name" />

              <Bar dataKey="cases" fill={RandomColor()}>
                {data.map((entry, index) => {
                  const color = RandomColor();
                  return (
                    <Cell
                      name={entry.name}
                      key={`cell-${index}`}
                      fill={color}
                    />
                  );
                })}
              </Bar>
            </BarChart>
          </ResponsiveContainer>
        </div>
      </div>
    );
  }
}
