import React, { Component } from "react";
import "./dataInput.scss";
import { Redirect } from "react-router-dom";
import SourceService from "../../services/sourceServices";

export default class dataInput extends Component {
  state = {
    ready: false,
    intensive: 0,
    hospital: 0,
    nationality: {
      in: 0,
      out: 0
    },
    source: {
      local: 0,
      import: 0
    },
    city: {
      Riyadh: 0,
      Jeddah: 0,
      Makkah: 0,
      Medina: 0,
      Dammam: 0,
      Taif: 0,
      Tabuk: 0,
      Al_Kharj: 0,
      Buraidah: 0
    }
  };
  constructor(props) {
    super(props);
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  componentDidMount() {
    SourceService.getCases(2).then(data => {
      this.setState({
        intensive: data.intensive,
        hospital: data.hospital,
        nationality: {
          in: data.nationality.in,
          out: data.nationality.out
        },
        source: {
          local: data.source.local,
          import: data.source.import
        },
        city: {
          Riyadh: data.city.Riyadh,
          Jeddah: data.city.Jeddah,
          Makkah: data.city.Makkah,
          Medina: data.city.Medina,
          Dammam: data.city.Dammam,
          Taif: data.city.Taif,
          Tabuk: data.city.Tabuk,
          Al_Kharj: data.city.Al_Kharj,
          Buraidah: data.city.Buraidah
        }
      });
      this.setState({ ready: true });
    });
  }
  updateValue(val, name, e) {
    val[name] = e.target.value;
    this.setState({ val });
  }
  handleSubmit(e) {
    localStorage.removeItem("Auth");
    let data = this.state;
    delete data["val"];
    SourceService.setCases(data);
    this.forceUpdate();
  }
  render() {
    let auth = localStorage.getItem("Auth");
    if (!auth) return <Redirect to="/login"></Redirect>;
    if (this.state.ready === true)
      return (
        <div id="dashboard" className="container">
          <div className="columns flex-centered my-2">
            <div style={{ width: "60%" }}>
              <form onSubmit={this.handleSubmit}>
                <div className="d-flex justify-content-center">
                  <h1 className="mainColor">COVID Data Update</h1>
                </div>
                <hr></hr>
                <h4>Cases</h4>
                <div className="form-row">
                  <div className="col-md-4 mb-3">
                    <label className="form-label" htmlFor="validationDefault01">
                      hospitalized cases
                    </label>
                    <input
                      type="number"
                      min="0"
                      value={this.state.hospital}
                      onChange={e => {
                        this.setState({
                          hospital: e.target.value
                        });
                      }}
                      className="form-input input-lg"
                    />
                  </div>
                  <div className="col-md-4 mb-3">
                    <label className="form-label" htmlFor="validationDefault01">
                      cases in intensive care
                    </label>
                    <input
                      type="number"
                      min="0"
                      value={this.state.intensive}
                      onChange={e => {
                        this.setState({
                          intensive: e.target.value
                        });
                      }}
                      className="form-input input-lg"
                    />
                  </div>
                </div>
                <h4>Nationality</h4>
                <div className="form-row">
                  <div className="col-md-4 mb-3">
                    <label className="form-label" htmlFor="validationDefault01">
                      Saudi Arabian
                    </label>
                    <input
                      type="number"
                      min="0"
                      value={this.state.nationality.in}
                      onChange={e =>
                        this.updateValue(this.state.nationality, "in", e)
                      }
                      className="form-input input-lg"
                    />
                  </div>
                  <div className="col-md-4 mb-3">
                    <label className="form-label" htmlFor="validationDefault01">
                      Other Nationality
                    </label>
                    <input
                      type="number"
                      min="0"
                      value={this.state.nationality.out}
                      onChange={e =>
                        this.updateValue(this.state.nationality, "out", e)
                      }
                      className="form-input input-lg"
                    />
                  </div>
                </div>
                <h4>Transmission Source</h4>
                <div className="form-row">
                  <div className="col-md-4 mb-3">
                    <label className="form-label" htmlFor="validationDefault01">
                      Local
                    </label>
                    <input
                      type="number"
                      min="0"
                      value={this.state.source.local}
                      onChange={e =>
                        this.updateValue(this.state.source, "local", e)
                      }
                      className="form-input input-lg"
                    />
                  </div>
                  <div className="col-md-4 mb-3">
                    <label className="form-label" htmlFor="validationDefault01">
                      Imported
                    </label>
                    <input
                      type="number"
                      min="0"
                      value={this.state.source.import}
                      onChange={e =>
                        this.updateValue(this.state.source, "import", e)
                      }
                      className="form-input input-lg"
                    />
                  </div>
                </div>
                <h4>Numbers By City</h4>
                <div className="form-row">
                  <div className="col-md-8 mb-3">
                    <div className="input-group">
                      <span className="input-group-addon addon-lg">
                        Riyadh{" "}
                      </span>
                      <input
                        type="number"
                        min="0"
                        value={this.state.city.Riyadh}
                        onChange={e =>
                          this.updateValue(this.state.city, "Riyadh", e)
                        }
                        className="form-input input-lg"
                        placeholder="Riyadh"
                      />
                    </div>
                  </div>
                </div>
                <div className="form-row">
                  <div className="col-md-8 mb-3">
                    <div className="input-group">
                      <span className="input-group-addon addon-lg">
                        Jeddah{" "}
                      </span>
                      <input
                        type="number"
                        min="0"
                        value={this.state.city.Jeddah}
                        onChange={e =>
                          this.updateValue(this.state.city, "Jeddah", e)
                        }
                        className="form-input input-lg"
                        placeholder="Jeddah "
                      />
                    </div>
                  </div>
                </div>
                <div className="form-row">
                  <div className="col-md-8 mb-3">
                    <div className="input-group">
                      <span className="input-group-addon addon-lg">
                        Makkah{" "}
                      </span>
                      <input
                        type="number"
                        min="0"
                        value={this.state.city.Makkah}
                        onChange={e =>
                          this.updateValue(this.state.city, "Makkah", e)
                        }
                        className="form-input input-lg"
                        placeholder="Makkah "
                      />
                    </div>
                  </div>
                </div>
                <div className="form-row">
                  <div className="col-md-8 mb-3">
                    <div className="input-group">
                      <span className="input-group-addon addon-lg">
                        {" "}
                        Medina{" "}
                      </span>
                      <input
                        type="number"
                        min="0"
                        value={this.state.city.Medina}
                        onChange={e =>
                          this.updateValue(this.state.city, "Medina", e)
                        }
                        className="form-input input-lg"
                        placeholder="	Medina "
                      />
                    </div>
                  </div>
                </div>
                <div className="form-row">
                  <div className="col-md-8 mb-3">
                    <div className="input-group">
                      <span className="input-group-addon addon-lg">Dammam</span>
                      <input
                        type="number"
                        min="0"
                        value={this.state.city.Dammam}
                        onChange={e =>
                          this.updateValue(this.state.city, "Dammam", e)
                        }
                        className="form-input input-lg"
                        placeholder="	Dammam  "
                      />
                    </div>
                  </div>
                </div>
                <div className="form-row">
                  <div className="col-md-8 mb-3">
                    <div className="input-group">
                      <span className="input-group-addon addon-lg">Ta'if</span>
                      <input
                        type="number"
                        min="0"
                        value={this.state.city.Taif}
                        onChange={e =>
                          this.updateValue(this.state.city, "Taif", e)
                        }
                        className="form-input input-lg"
                        placeholder="	Ta'if   "
                      />
                    </div>
                  </div>
                </div>
                <div className="form-row">
                  <div className="col-md-8 mb-3">
                    <div className="input-group">
                      <span className="input-group-addon addon-lg">Tabuk</span>
                      <input
                        type="number"
                        min="0"
                        value={this.state.city.Tabuk}
                        onChange={e =>
                          this.updateValue(this.state.city, "Tabuk", e)
                        }
                        className="form-input input-lg"
                        placeholder="	Tabuk "
                      />
                    </div>
                  </div>
                </div>
                <div className="form-row">
                  <div className="col-md-8 mb-3">
                    <div className="input-group">
                      <span className="input-group-addon addon-lg">
                        Al Kharj
                      </span>
                      <input
                        type="number"
                        min="0"
                        value={this.state.city.Al_Kharj}
                        onChange={e =>
                          this.updateValue(this.state.city, "Al_Kharj", e)
                        }
                        className="form-input input-lg"
                        placeholder="	Al Kharj  "
                      />
                    </div>
                  </div>
                </div>
                <div className="form-row">
                  <div className="col-md-8 mb-3">
                    <div className="input-group">
                      <span className="input-group-addon addon-lg">
                        Buraidah
                      </span>
                      <input
                        type="number"
                        min="0"
                        value={this.state.city.Buraidah}
                        onChange={e =>
                          this.updateValue(this.state.city, "Buraidah", e)
                        }
                        className="form-input input-lg"
                        placeholder="	Buraidah "
                      />
                    </div>
                  </div>
                </div>
                <button className="btn btn-primary" type="submit">
                  Submit New Data
                </button>
              </form>
            </div>
          </div>
        </div>
      );
    else return <div>Loading...</div>;
  }
}
