import axios from "axios";
import { Case } from "../models/case";

const Source1Url = "https://pomber.github.io/covid19/timeseries.json";
const Source2Url = "/api/data";
const SourceService = {
  getCases: function(src: Number) {
    switch (src) {
      case 1:
        return new Promise((resolve, reject) => {
          axios.get(Source1Url).then(res => {
            res.data
              ? resolve(
                  (<Case[]>res.data["Saudi Arabia"]).filter(
                    x =>
                      !(x.confirmed == 0 && x.deaths == 0 && x.recovered == 0)
                  )
                )
              : reject("no Data!");
          });
        });
      case 2:
        return new Promise((resolve, reject) => {
          axios
            .get(Source2Url, {
              headers: {
                secret: "CoolSecret123"
              }
            })
            .then(res => {
              res.data ? resolve(res.data) : reject("no Data!");
            });
        });
    }
  },
  setCases: function(data: {}) {
    return new Promise((resolve, reject) => {
      axios
        .post(Source2Url, data, {
          headers: {
            secret: "CoolSecret123"
          }
        })
        .then(res => {
          res.data ? resolve(res.data) : reject("no Data!");
        });
    });
  }
};
export default SourceService;
