import React, { Component } from "react";
import "./pieChart.scss";
import RandomColor from "randomcolor";
import {
  PieChart,
  Legend,
  Pie,
  Cell,
  Tooltip,
  ResponsiveContainer
} from "recharts";
export default class pieChart extends Component {
  state = {
    data: [
      { name: "Saudi Arabian", value: 400 },
      { name: "Other Nationality", value: 80 }
    ]
  };
  render() {
    const { data } = this.props;
    return (
      <div style={{ width: "100%", height: 300 }}>
        <ResponsiveContainer>
          <PieChart onMouseEnter={this.onPieEnter}>
            <Tooltip />
            <Legend />
            <Pie
              data={data}
              cx="50%"
              cy="50%"
              innerRadius={50}
              outerRadius={100}
              fill="#8884d8"
              paddingAngle={1}
              dataKey="value"
            >
              {data.map((entry, index) => (
                <Cell
                  name={entry.name}
                  key={`cell-${index}`}
                  fill={RandomColor()}
                />
              ))}
            </Pie>
          </PieChart>
        </ResponsiveContainer>
      </div>
    );
  }
}
