import React, { Component } from "react";
import "./dashboard.scss";
import DateRangePicker from "react-bootstrap-daterangepicker";
import moment from "moment";
import LineChart from "../elements/rechart";
import TimeLine from "../elements/timeline";
import BarChart from "../elements/barChart";
import BarChartStack from "../elements/barChartStack";
import PieChart from "../elements/pieChart";
import SourceService from "../../services/sourceServices";

export default class dashboard extends Component {
  state = {
    intensive: 0,
    hospital: 0,
    total: 0,
    confirmed: 0,
    recovered: 0,
    deaths: 0,
    critical: 0,
    barCases2: [],
    date: {
      start: moment(),
      end: moment()
    },
    nationality: {
      in: 0,
      out: 0
    },
    source: {
      local: 0,
      import: 0
    },
    city: {
      Riyadh: 0,
      Jeddah: 0,
      Makkah: 0,
      Medina: 0,
      Dammam: 0,
      Taif: 0,
      Tabuk: 0,
      Al_Kharj: 0,
      Buraidah: 0
    },
    ready: false
  };
  componentDidMount() {
    SourceService.getCases(1).then(data => {
      this.setState({ barCases2: data });
      let vc = data[data.length - 1];
      this.setState({
        total: vc.confirmed + vc.recovered + vc.deaths,
        confirmed: vc.confirmed,
        recovered: vc.recovered,
        deaths: vc.deaths
      });
      SourceService.getCases(2).then(data => {
        this.setState({
          intensive: data.intensive,
          hospital: data.hospital,
          nationality: {
            in: data.nationality.in,
            out: data.nationality.out
          },
          source: {
            local: data.source.local,
            import: data.source.import
          },
          city: {
            Riyadh: data.city.Riyadh,
            Jeddah: data.city.Jeddah,
            Makkah: data.city.Makkah,
            Medina: data.city.Medina,
            Dammam: data.city.Dammam,
            Taif: data.city.Taif,
            Tabuk: data.city.Tabuk,
            Al_Kharj: data.city.Al_Kharj,
            Buraidah: data.city.Buraidah
          }
        });
        this.setState({ ready: true });
      });
    });
  }
  formatData(chart) {
    let res = [];
    switch (chart) {
      case "pie1":
        res.push({
          name: "Saudi Arabian",
          value: parseInt(this.state.nationality.in)
        });
        res.push({
          name: "Other Nationality",
          value: parseInt(this.state.nationality.out)
        });
        break;
      case "pie2":
        res.push({ name: "Local", value: parseInt(this.state.source.local) });
        res.push({
          name: "Imported",
          value: parseInt(this.state.source.import)
        });
        break;
      case "bar1":
        res.push({ name: "Riyadh", cases: this.state.city.Riyadh });
        res.push({ name: "Tabuk", cases: this.state.city.Tabuk });
        res.push({ name: "Ta'if", cases: this.state.city.Taif });
        res.push({ name: "Jeddah", cases: this.state.city.Jeddah });
        res.push({ name: "Makkah", cases: this.state.city.Makkah });
        res.push({ name: "Medina", cases: this.state.city.Medina });
        res.push({ name: "Dammam", cases: this.state.city.Dammam });
        res.push({ name: "Al Kharj", cases: this.state.city.Al_Kharj });
        res.push({ name: "Buraidah", cases: this.state.city.Buraidah });
        break;
    }
    return res;
  }
  handlePick = (event, picker) => {
    this.setState({
      date: { start: picker.startDate, end: picker.endDate }
    });
  };
  render() {
    if (this.state.ready === true)
      return (
        <div id="dashboard" className="container">
          <div className="columns flex-centered my-2">
            <div className="column col-sm-12 col-9">
              <h5>Coronavirus Outbreak in Saudi Arabia</h5>
            </div>
            <div className="column col-sm-12 col-3">
              <DateRangePicker
                buttonClasses={["btn"]}
                startDate="1/1/2020"
                onEvent={this.handlePick}
              >
                <button className="btn form-input date-wrapper">
                  <span className="date">
                    {this.state.date.start.format("D MMM YYYY")}
                  </span>
                  <span className="date-seperator">→</span>
                  <span className="date">
                    {this.state.date.end.format("D MMM YYYY")}
                  </span>
                </button>
              </DateRangePicker>
            </div>
          </div>
          <div className="number-graph-wrapper">
            <div className="number-graph mr-2">
              <div className="card">
                <div className="card-header">
                  <div className="card-title ">
                    <h6>
                      <b>CONFIRMED CASES</b>
                    </h6>
                  </div>
                </div>
                <div className="card-body status-confirmed">
                  <div className="cases-container">
                    <h2 className="case">{this.state.confirmed}</h2>
                    <span className="info">
                      {((this.state.confirmed / this.state.total) * 100)
                        .toString()
                        .substring(0, 3)}
                      % of total cases
                    </span>
                  </div>
                  <div id="lineChartContainer">
                    <LineChart
                      data={this.state.barCases2}
                      name="confirmed"
                    ></LineChart>
                  </div>
                </div>
              </div>
            </div>
            <div className="number-graph mr-2">
              <div className="card">
                <div className="card-header">
                  <div className="card-title ">
                    <h6>
                      <b>HOSPITALIZED CASES</b>
                    </h6>
                  </div>
                </div>
                <div className="card-body status-hospitalized">
                  <div className="cases-container">
                    <h2 className="case">{this.state.hospital}</h2>
                    <span className="info">
                      {((this.state.hospital / this.state.total) * 100)
                        .toString()
                        .substring(0, 3)}
                      % of total cases
                    </span>
                  </div>
                  <div id="lineChartContainer">
                    <LineChart
                      data={this.state.barCases2}
                      name="deaths"
                    ></LineChart>
                  </div>
                </div>
              </div>
            </div>
            <div className="number-graph mr-2">
              <div className="card">
                <div className="card-header">
                  <div className="card-title ">
                    <h6>
                      <b>CASES IN INTENSIVE CARE</b>
                    </h6>
                  </div>
                </div>
                <div className="card-body status-icu">
                  <div className="cases-container">
                    <h2 className="case">{this.state.intensive}</h2>
                    <span className="info">
                      {((this.state.intensive / this.state.total) * 100)
                        .toString()
                        .substring(0, 3)}
                      % of total cases
                    </span>
                  </div>
                  <div id="lineChartContainer">
                    <LineChart
                      data={this.state.barCases2}
                      name="deaths"
                    ></LineChart>
                  </div>
                </div>
              </div>
            </div>
            <div className="number-graph mr-2">
              <div className="card">
                <div className="card-header">
                  <div className="card-title ">
                    <h6>
                      <b>RECOVERED CASES</b>
                    </h6>
                  </div>
                </div>
                <div className="card-body status-recovered">
                  <div className="cases-container">
                    <h2 className="case">{this.state.recovered}</h2>
                    <span className="info">
                      {((this.state.recovered / this.state.total) * 100)
                        .toString()
                        .substring(0, 3)}
                      % of total cases
                    </span>
                  </div>
                  <div id="lineChartContainer">
                    <LineChart
                      data={this.state.barCases2}
                      name="recovered"
                    ></LineChart>
                  </div>
                </div>
              </div>
            </div>
            <div className="number-graph mr-2">
              <div className="card">
                <div className="card-header">
                  <div className="card-title ">
                    <h6>
                      <b>DEATHS</b>
                    </h6>
                  </div>
                </div>
                <div className="card-body status-died">
                  <div className="cases-container">
                    <h2 className="case">{this.state.deaths}</h2>
                    <span className="info">
                      {((this.state.deaths / this.state.total) * 100)
                        .toString()
                        .substring(0, 3)}
                      % of total cases
                    </span>
                  </div>
                  <div id="lineChartContainer">
                    <LineChart
                      data={this.state.barCases2}
                      name="deaths"
                    ></LineChart>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className="columns my-2">
            <div className="column col-sm-12 col-12">
              <div className="card">
                <div className="card-header">
                  <div className="card-title h6">Rise Trend</div>
                  <div className="card-subtitle text-gray mt-1">
                    30th Jan 2020 to Today
                  </div>
                </div>
                <div className="card-body">
                  <TimeLine data={this.state.barCases2}></TimeLine>
                </div>
              </div>
            </div>
          </div>
          <div className="columns my-2">
            <div className="column col-sm-12 col-3">
              <div className="card">
                <div className="card-header">
                  <div className="card-title h6">Nationality</div>
                </div>
                <div className="card-body">
                  <div id="pieChartContainer">
                    <PieChart data={this.formatData("pie1")}></PieChart>
                  </div>
                </div>
              </div>
            </div>
            <div className="column col-sm-12 col-6">
              <div className="card">
                <div className="card-header">
                  <div className="card-title h6">States</div>
                </div>
                <div className="card-body">
                  <div
                    id="verticalBarChartContainer"
                    className="chartDimensions"
                  >
                    <BarChart data={this.formatData("bar1")}></BarChart>
                  </div>
                </div>
              </div>
            </div>
            <div className="column col-sm-12 col-3">
              <div className="card">
                <div className="card-header">
                  <div className="card-title h6">Transmission Source</div>
                </div>
                <div className="card-body">
                  <div id="pieChartContainer">
                    <PieChart data={this.formatData("pie2")}></PieChart>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className="columns my-2">
            <div className="column col-sm-12 col-12">
              <div className="card">
                <div className="card-header">
                  <div className="card-title h6">Confirmed COVID-19 Cases</div>
                  <div className="card-subtitle text-gray mt-1">
                    22th Jan 2020 to Today
                  </div>
                </div>
                <div className="card-body">
                  <div
                    id="stackedBarChartContainer"
                    className="stackedBarChartDimensions"
                  >
                    <BarChartStack data={this.state.barCases2}></BarChartStack>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      );
    else return <div>Loading ...</div>;
  }
}
